@import url(https://fonts.googleapis.com/css2?family=B612&display=swap);
html {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: B612, sans-serif;
  background: linear-gradient(89deg, rgb(255, 203, 33), rgb(20, 181, 192));
  overflow-x: hidden;
  letter-spacing: 1px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.particles {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
}

.navigation {
  cursor: pointer;
  position: relative;
  top: 10px;
  left: 92%;
  font-size: 18px;
}

.imagelink-container {
  margin-top: -50px;
}

.searchbox {
  width: 400px;
  height: 35px;
  font-size: 18px;
  background: whitesmoke;
  border-radius: 7px;
  border: none;
  box-shadow: 0 2px 5px 2px rgba(0, 0, 0, 0.2);
  text-align: center;
  padding: 10px;
  font-family: B612, sans-serif;
}

.searchbox:focus {
  outline: none;
}

button {
  width: 120px;
  height: 35px;
  font-size: 18px;
  background: transparent;
  color: #333;
  border-radius: 7px;
  border: 3px solid green;
  text-align: center;
  cursor: pointer;
  margin-left: 20px;
  font-family: B612, sans-serif;
  letter-spacing: 1px;
}

button:hover {
  background: green;
  color: whitesmoke;
  border: none;
  box-shadow: 0 2px 5px 2px rgba(0, 0, 0, 0.3);
}

.container {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.signin-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 400px;
  height: 450px;
  box-shadow: 0 5px 10px 5px rgba(0, 0, 0, 0.3);
  border-radius: 7px;
}

h1 {
  font-size: 34px;
  margin-top: -30px;
}

#email {
  width: 60%;
  height: 30px;
  background: transparent;
  border-radius: 7px;
  text-align: center;
  font-size: 15px;
  margin: 10px;
}

#email:focus {
  outline: none;
}
#password {
  width: 60%;
  height: 30px;
  background: transparent;
  border-radius: 7px;
  text-align: center;
  font-size: 15px;
  margin: 10px;
}

#password:focus {
  outline: none;
}

#signin {
  margin-bottom: 10px;
  margin-top: 20px;
  background: green;
  color: white;
}

#signin:active {
  -webkit-transform: scale(95%);
          transform: scale(95%);
}

#register {
  border: none;
}

#register:hover {
  background: none;
  box-shadow: none;
  color: black;
}

#register:active {
  -webkit-transform: scale(95%);
          transform: scale(95%);
}

@media screen and (max-width: 600px) {
  .signin-container {
    width: 95%;
  }
}

